<template>
  <!--  -->
  <div class="bqteam-main-wrap-inner">
    <!--  -->
    <el-button class="mb" icon="el-icon-plus" @click="addUserDialogVisible = true"></el-button>

    <!--  -->
    <el-table :data="userArray" border v-loading="loading">
      <el-table-column prop="username" label="" width="120" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" width="120" align="center">
      </el-table-column>
      <el-table-column prop="roleName" label="" width="120" align="center">
      </el-table-column>
      <el-table-column prop="tel" label="" width="120" align="center">
      </el-table-column>
      <el-table-column prop="email" label="" align="center">
      </el-table-column>
      <el-table-column prop="statusString" label="" width="120" align="center">
      </el-table-column>
      <el-table-column label="" width="160">
        <template slot-scope="scope" fixed="right" label="">
          <button class="table-button" @click="editType(scope.$index)" type="text"></button>
          <button class="table-button" @click="disableUser(scope.$index)" type="text">{{scope.row.operateString}}</button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="mt">
      <el-pagination @current-change="pageChanged" :current-page="pageNum" layout="total, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>

    <div>
      <!--  -->
      <el-dialog title="" :visible.sync="addUserDialogVisible">
        <el-form :model="addUserForm" :rules="addUserFormRules" ref="addUserForm" label-width="80px">
          <el-form-item label="" prop="username">
            <el-input v-model="addUserForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="addUserForm.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="idNumber">
            <el-input v-model="addUserForm.idNumber"></el-input>
          </el-form-item>
          <el-form-item label="" prop="tel">
            <el-input v-model="addUserForm.tel"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addUserForm.email"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addUserDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="editRoleTypeDialogVisible">
        <el-radio-group v-model="roleId">
          <el-radio v-for="role in roleArray" :key="role.roleId" :label="role.roleId">{{role.roleName}}</el-radio>
        </el-radio-group>

        <div slot="footer" class="dialog-footer">
          <el-button @click="editRoleTypeDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editTypeConfirm"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userArray: [],
      roleArray: [],

      pageNum: 1,
      total: 0,

      addUserDialogVisible: false,
      addUserForm: {
        username: '',
        name: '',
        idNumber: '',
        tel: '',
        email: '',
        type: 1
      },
      addUserFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 18, message: ' 4  18 ', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
        ],
        idNumber: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'string', len: 18, message: '18', trigger: 'blur' }
        ],
        tel: [
          { required: false, message: '', trigger: 'blur' },
          { type: 'string', len: 11, message: '11', trigger: 'blur' }
        ],
        email: [
          { required: false, message: '', trigger: 'blur' },
          { type: 'string', min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ]
      },

      editRoleTypeDialogVisible: false,
      editRoleTypeIndex: 0,
      roleId: 0,

      loading: false,
    };
  },
  mounted () {
    this.getUsers()
    this.getRoles()
  },
  methods: {
    getUsers () {
      this.loading = true
      this.getRequest(this.httpUrl.sysUser, { pageSize: 10, pageNum: this.pageNum })
        .then(response => {
          let statusStrings = ['', '', '', '']
          let operateStrings = ['', '', '', '']
          this.loading = false
          this.userArray = []
          response.data.list.forEach(element => {
            element.statusString = statusStrings[element.status]
            element.operateString = operateStrings[element.status]
            if (element.status !== 3) {
              this.userArray.push(element)
            }
          })
          this.total = this.userArray.length
        })
        .catch(error => {
          this.loading = false
        })
    },

    getRoles () {
      this.getRequest(this.httpUrl.sysRole, {})
        .then(response => {
          this.roleArray = response.data.list
        })
        .catch(error => {
        })
    },

    addUser () {
      this.$refs.addUserForm.validate((valid) => {
        if (valid) {
          this.postRequest(this.httpUrl.sysUser, this.addUserForm)
            .then(response => {
              this.addUserDialogVisible = false
              this.getUsers()
            })
            .catch(error => {
            })
        } else {
          return false;
        }
      })
    },

    editType (index) {
      this.roleId = this.userArray[index].roleId
      this.editRoleTypeIndex = index
      this.editRoleTypeDialogVisible = true
    },

    editTypeConfirm () {
      this.editRoleTypeDialogVisible = false

      let parmas = { username: this.userArray[this.editRoleTypeIndex].username, roleId: this.roleId }
      this.putRequest(this.httpUrl.sysUserRole, parmas)
        .then(response => {
          this.getUsers()
        })
        .catch(error => {
        })
    },

    disableUser (index) {
      let user = this.userArray[index]
      user.lock = user.status === 0 ? 1 : 0
      this.putRequest(this.httpUrl.sysUser, user)
        .then(response => {
          this.getUsers()
        })
        .catch(error => {
        })
    },

    pageChanged (page) {
      this.pageNum = page
      this.getUsers()
    }
  }
};
</script>

<style lang="scss" scoped>
.el-input {
  width: 300px;
}
</style>




