<template>
  <div>
    <!--  -->
    <div class="search-box">
      <cus-radio v-model="paperCondition.paperGrade" :values="radioGrades" title="" defaultRadio="" />
      <cus-radio v-model="paperCondition.paperType" :values="radioTypes" title="" defaultRadio="" />
    </div>

    <el-button style="margin:10px 0px;" @click="dialogVisible=true"></el-button>

    <!--  -->
    <el-table :data="paperList" style="width: 100%" v-loading="loading" empty-text="" border>

      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <button class="table-button" @click="toPaperPreview(scope.$index)">{{scope.row.paperTitle}}</button>
        </template>
      </el-table-column>
      <el-table-column prop="grade" label="" width="90" align="center">
      </el-table-column>
      <el-table-column prop="type" label="" width="90" align="center">
      </el-table-column>
      <el-table-column prop="count" label="" width="90" align="center">
      </el-table-column>
      <el-table-column prop="useTimes" label="" width="90" align="center">
      </el-table-column>
      <el-table-column label="" width="90" align="center">
        <template slot-scope="scope">
          <button class="table-button" @click="clickShareBtn(scope.$index)">{{scope.row.isShareString}}</button>
        </template>
      </el-table-column>
      <el-table-column prop="date" label="" width="220" align="center">
      </el-table-column>
      <el-table-column label="" width="120" align="center">
        <template slot-scope="scope">
          <button class="table-button" @click="toPaperEdit(scope.$index)"></button>
          <button class="table-button" @click="removePaper(scope.row.paperId)"></button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="mt">
      <el-pagination @current-change="handleCurrentChange" :current-page="paperCondition.pageNum" layout="total, prev, pager, next, jumper" :total="paperTotal">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%">
      <el-form ref="form" :model="paper" label-width="80px">
        <el-form-item label="">
          <el-select v-model="paper.grade" placeholder="">
            <el-option v-for="item in radioGrades" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="paper.type">
            <el-radio v-for="item in radioTypes" :key="item.id" :label="item.id">{{item.name}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="paperTitle"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="newPaper">  </el-button>
      </div>
    </el-dialog>

    <!-- / -->
    <el-dialog :title="shareDialogTitle" :visible.sync="shareDialogVisible" width="50%">
      <span>{{shareDialogMsg}}</span>
      <div slot="footer" class="dialog-footer">
        <el-button @click="shareDialogVisible = false"> </el-button>
        <el-button type="primary" @click="updateisShare"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { SET_GRADE_KNOWLEDGE } from "@/store/mutation-types";

export default {
  name: "paper-mine",
  data () {
    return {
      paperCondition: {
        paperSubject: this.$store.getters.getSubject(),
        paperGrade: 0,
        paperType: 0,
        pageNum: 1,
        pageSize: 10
      },
      paper: {
        title: "",
        type: 0,
        grade: 0,
      },
      dialogVisible: false,

      paperList: [],

      paperTotal: 10,

      loading: false,

      shareDialogVisible: false,
      // 
      shareIndex: -1,
      // 
      shareDialogTitle: '',
      shareDialogMsg: ''
    };
  },
  watch: {
    paperCondition: {
      handler (newVal, oldVal) {
        this.getPaperList()
      },
      deep: true
    }
  },
  mounted () {
    this.getPaperList();
  },
  computed: {
    createBtnDisabled () {
      return this.paper.title === "";
    },

    radioGrades () {
      return this.$store.getters.getGrades()
    },

    radioTypes () {
      let paperTypes = this.$store.state.paperType.paperTypes
      if (paperTypes === null) {
        this.$store.dispatch('getPaperTypes')
        return []
      }
      return paperTypes
    },

    paperTitle: {
      get: function () {
        let gradeString = this.getValue(this.global.grades, this.paper.grade)
        let subjectString = this.$store.getters.getSubjectName()
        let typeString = this.paper.type === 0 ? '' : this.getValue(this.radioTypes, this.paper.type)
        this.paper.title = gradeString + subjectString + typeString
        return this.paper.title
      },
      // setter
      set: function (newValue) {
        return newValue
      }
    }
  },
  methods: {
    // 
    newPaper () {
      this.dialogVisible = false
      let obj = {
        paperHead: {
          headlineText: "",
          subheadText: "",
          paperInfoText: "xxx90xxx",
          studentInfoText:
            "___________ ___________ ___________ ___________",
          announcementsText:
            "1 2"
        },
        exerciseInfoList: [],
        exerciseListClassified: []
      }
      let params = {
        paperTitle: this.paper.title,
        paperStatus: 0, //
        paperGrade: this.paper.grade,
        paperSubject: this.$store.getters.getSubject(),
        paperType: this.paper.type,
        isShare: 0, //
        paperJson: JSON.stringify(obj)
      }
      this.postRequest(this.httpUrl.paper, params).then(response => {
        this.getPaperList()
      })
    },

    toEditPaper () {
      this.dialogVisible = false;
      this.$router.push("paperEdit");
    },
    // 
    getPaperList () {
      this.loading = true;
      let params = this.deleteUselessAttribute(this.paperCondition)
      this.getRequest(this.httpUrl.paper, params).then(response => {
        this.loading = false

        this.paperTotal = response.data.total

        this.paperList = response.data.list
        this.paperList.forEach(paper => {
          let jsonObj = JSON.parse(paper.paperJson);
          let count = jsonObj.exerciseInfoList.length
          paper.count = count

          paper.grade = this.getValue(this.global.grades, paper.paperGrade)
          paper.type = this.getValue(this.radioTypes, paper.paperType)
          paper.isShareString = paper.isShare === 0 ? '' : ''
          paper.date = this.readStandardDate(paper.updateTime, 'minute')
        })
      }).catch(error => {
        this.loading = false;
      })
    },

    // 
    handleCurrentChange (page) {
      this.paperCondition.pageNum = page
      this.getPaperList()
    },

    // 
    removePaper (id) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {
        this.loading = true;
        let params = { paperId: id }
        this.deleteRequest(this.httpUrl.paper, params).then(response => {
          this.loading = false;
          this.pm("", "success");
          this.getPaperList();
        })
      })
    },

    toPaperEdit (index) {
      this.$store.commit("setPaper", this.paperList[index]);
      this.$router.push("paperEdit");
    },

    toPaperPreview (index) {
      this.$store.commit("setPaper", this.paperList[index]);
      this.$router.push({ name: "paperPreview" })
    },

    clickShareBtn (index) {
      this.shareDialogVisible = true
      this.shareIndex = index
      this.shareDialogTitle = this.paperList[index].isShare === 0 ? '' : ''
      this.shareDialogMsg = this.paperList[index].isShare === 0 ? '' : ''
    },

    updateisShare () {
      this.shareDialogVisible = false
      let isShare = this.paperList[this.shareIndex].isShare === 0 ? 1 : 0
      let params = { paperId: this.paperList[this.shareIndex].paperId, isShare: isShare }
      this.loading = true
      this.putRequest(this.httpUrl.paper, params).then(response => {
        this.loading = false
        this.getPaperList()
      }).catch(error => {
        this.loading = false
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.new-form {
  width: 600px;
}
</style>



