// tab
const state = {
  //  tab
  exercisePageTab: '1',
  //  tab
  paperPageTab: '1',
  //  tab
  xmbPageTab: '1',
  //  tab
  zskPageTab: '1'
}

const mutations = {
  setExercisePageTab: (state, tab) => {
    state.exercisePageTab = tab
  },

  setPaperPageTab: (state, tab) => {
    state.paperPageTab = tab
  },

  setXmbPageTab: (state, tab) => {
    state.xmbPageTab = tab
  },

  setZskPageTab: (state, tab) => {
    state.zskPageTab = tab
  },
}

const tab = {
  state,
  mutations
}

export default tab



