<template>
  <div>
    <!--  -->
    <div>
      <!--  -->
      <div class="knowledge-grade border">
        <div class="box-head">
          <div class="label"></div>
        </div>
        <div class="box-main">
          <el-tree ref="treeGrade" :data="grades" node-key="id" :highlight-current="true" @node-click="selectGrade" :default-expand-all="true" :expand-on-click-node="false">
            <span slot-scope="{ node, data }">{{ node.label }}</span>
          </el-tree>
        </div>

      </div>

      <!--  -->
      <div class="knowledge-knowledge border">
        <div class="box-head">
          <div class="label"></div>
          <el-button class="ml" type="text" @click="edit = !edit">{{!edit ? '' : ''}}</el-button>
        </div>

        <div class="box-main">
          <el-tree ref="treeKnowledge" :data="knowledgeTree" node-key="id" :highlight-current="true" @node-click="selectKnowledge" :default-expand-all="true" :expand-on-click-node="false">
            <span class="custom-tree-node" slot-scope="{ node, data }">
              <span>{{ node.label }}</span>
              <span>
                <el-button v-if="edit" type="text" icon="el-icon-edit" @click="editKnowledge(node)"></el-button>
              </span>
            </span>
          </el-tree>
        </div>
      </div>

      <!--  -->
      <div class="knowledge-detail border">
        <div class="box-head">
          <div class="label"></div>
          <el-button class="ml" v-show="editContent" type="text" @click="updateKnowledgeContent"></el-button>
          <el-button class="ml" type="text" @click="editContent = !editContent">{{!editContent ? '' : ''}}</el-button>
        </div>
        <div class="box-main">
          <div class="m" v-show="!editContent">{{selectedKnowledge.content}}</div>
          <div id="container-k" v-show="editContent" type="text/plain" style="height:80%"></div>
        </div>
      </div>
    </div>

    <el-dialog title="" :visible.sync="editDialogVisible">
      <span slot="footer" class="dialog-footer">
        <el-button @click="editKnowledgeMode(0)"></el-button>
        <el-button @click="editKnowledgeMode(1)"></el-button>
        <el-button @click="editKnowledgeMode(2)"></el-button>
        <el-button @click="editKnowledgeMode(3)"></el-button>
        <el-button @click="editDialogVisible = false"></el-button>
      </span>
    </el-dialog>

    <el-dialog :title="editTitles[editMode]" :visible.sync="editFormDialogVisible">
      <el-form v-if="editMode < 2">
        <el-form-item label="" label-width="90px">
          <el-input v-model="inputKnowledgeName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <span v-if="editMode === 2">
        
        <span class="knowledge-highlight">{{selectedKnowledge.label}}</span>
        <span>{{selectedKnowledge.children.length > 0 ? '' : ''}}</span>
        ?
      </span>

      <span v-if="editMode === 3">
        <span class="knowledge-highlight">{{selectedKnowledge.label}}</span>
      </span>
      <br><br>
      <el-form v-if="editMode === 3">
        <el-form-item label="" label-width="54px">
          <el-input v-model="inputKnowledgeName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editKnowledgeCancle"> </el-button>
        <el-button type="primary" @click="editKnowledgeDone"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import "@/../static/plugins/ueditor-math-frontend/ueditor.config.js"
import "@/../static/plugins/ueditor-math-frontend/ueditor.all.min.js"
import "@/../static/plugins/ueditor-math-frontend/lang/zh-cn/zh-cn.js"
import "@/../static/plugins/ueditor-math-frontend/third-party/kityformula-plugin/addKityFormulaDialog.js"
import "@/../static/plugins/ueditor-math-frontend/third-party/kityformula-plugin/defaultFilterFix.js"

export default {
  data () {
    return {
      // 
      grade: 7,

      // 
      selectedKnowledge: {},

      // 
      edit: false,
      editContent: false,

      // 
      editDialogVisible: false,
      editFormDialogVisible: false,

      // input
      inputKnowledgeName: '',
      inputKnowledgeContent: '',

      editMode: 0,
      editTitle: '',
      editTitles: ['', '', '', ''],
      parentLength: 0,

      //
      instance: null
    }
  },
  computed: {
    grades () {
      let grades1 = this.$store.getters.getGrades()
      let grades2 = []
      grades1.forEach(element => {
        let obj = { id: element.id, label: element.name }
        grades2.push(obj)
      })
      return grades2
    },

    //
    knowledgeTree () {
      let tree = this.$store.getters.knowledgeTree(this.grade, this.$store.getters.getSubject())
      if (tree.length > 0) {
        return tree
      } else {
        // this.knowledgeTree
        return [{
          children: [],
          grade: this.grade,
          id: '',
          label: '',
          level: 0,
          pnumber: 'k',
          content: '',
          subject: this.$store.getters.getSubject()
        }]
      }
    }
  },
  mounted () {
    this.initEditor()
    this.getKnowledgeList()
  },
  methods: {
    /**
     * 
     */
    initEditor () {
      this.$nextTick(() => {
        this.instance = UE.getEditor("container-k")
        this.instance.addListener("ready", () => {
          this.$emit("ready", this.instance)
          this.instance.setContent(this.selectedKnowledge.content)
        })
      })
    },

    // 
    getKnowledgeList () {
      if (this.$store.state.knowledge.knowledgeList.length === 0) {
        this.$store.dispatch('loading', true)
        this.getRequest(this.httpUrl.knowledge, { pageSize: 1000, pageNum: 1 }).then(response => {
          this.$store.dispatch('loading', false)
          this.$store.commit('setKnowledgeList', response.data.list)

          this.highLigthFirstKnowledge()
        }).catch(error => {
          this.$store.dispatch('loading', false)
        })
      } else {
        this.highLigthFirstKnowledge()
      }
    },

    // 
    highLigthFirstKnowledge () {
      this.$refs.treeGrade.setCurrentKey(this.grade)

      if (this.selectedKnowledge.id === undefined) {
        // 
        this.selectedKnowledge = this.knowledgeTree[0]
      }

      this.$nextTick(() => {
        this.$refs.treeKnowledge.setCurrentKey(this.selectedKnowledge.id)
      })
    },

    // 
    selectGrade (grade) {
      this.grade = grade.id
      this.inputKnowledgeContent = this.selectedKnowledge.content
      this.edit = false
      this.editContent = false
    },

    // 
    selectKnowledge (knowledge) {
      this.selectedKnowledge = knowledge
      this.inputKnowledgeContent = this.selectedKnowledge.content
      this.instance.setContent(this.inputKnowledgeContent)
      this.edit = false
      this.editContent = false
    },

    //
    editKnowledge (knowledge) {
      this.editDialogVisible = true
      this.selectedKnowledge = knowledge.data
    },

    // 
    editKnowledgeMode (mode) {
      this.editDialogVisible = false
      this.editFormDialogVisible = true

      this.editMode = mode
    },

    // 
    editKnowledgeCancle () {
      this.editFormDialogVisible = false
      this.inputKnowledgeName = ''
      this.inputKnowledgeContent = ''
    },

    // editMode
    editKnowledgeDone () {
      this.editFormDialogVisible = false
      switch (this.editMode) {
        case 0:
          this.addKnowledge()
          break;
        case 1:
          this.addSubKnowledge()
          break;
        case 2:
          this.removeKnowledge()
          break;
        case 3:
          this.updateKnowledge()
          break;
        default:
          break;
      }
    },

    // 
    addKnowledge () {
      // 
      let k = {}
      k.knowledgeName = this.inputKnowledgeName
      k.knowledgeGrade = this.selectedKnowledge.grade
      k.knowledgeSubject = this.selectedKnowledge.subject
      k.knowledgePNumber = this.selectedKnowledge.pnumber
      k.knowledgeLevel = this.selectedKnowledge.level
      k.knowledgeContent = ''
      k.knowledgeLatex = ''

      // number
      if (this.selectedKnowledge.id !== '') {
        this.getParentLength(this.knowledgeTree, this.selectedKnowledge.id)
        this.parentLength++
      } else {
        // this.selectedKnowledge.id === '' this.selectedKnowledge
        this.parentLength = 1
      }

      if (this.parentLength < 10) {
        this.parentLength = '0' + this.parentLength
      }

      // knowledgeNumber
      if (k.knowledgeLevel === 0) {
        // knowledgeNumber
        k.knowledgeNumber = '0' + k.knowledgeGrade + '0' + k.knowledgeSubject + this.parentLength
      } else {
        // knowledgeNumber
        k.knowledgeNumber = this.selectedKnowledge.pnumber + this.parentLength
      }

      this.$store.dispatch('createKnowledge', k)
    },

    // number
    getParentLength (tree, knowledgeNumber) {
      for (let i = 0; i < tree.length; i++) {
        const element = tree[i]
        if (element.id === knowledgeNumber) {
          this.parentLength = tree.length
        } else {
          if (element.children.length > 0) {
            this.getParentLength(element.children, knowledgeNumber)
          }
        }
      }
    },

    // 
    addSubKnowledge () {
      // 
      let k = {}
      k.knowledgeName = this.inputKnowledgeName
      k.knowledgeGrade = this.selectedKnowledge.grade
      k.knowledgeSubject = this.selectedKnowledge.subject
      k.knowledgePNumber = this.selectedKnowledge.id
      k.knowledgeContent = ''
      k.knowledgeLevel = this.selectedKnowledge.level + 1
      let childCount = this.selectedKnowledge.children.length
      if (childCount < 9) {
        childCount = '0' + ++childCount
      }
      k.knowledgeNumber = this.selectedKnowledge.id + childCount
      this.$store.dispatch('createKnowledge', k)
    },

    // 
    removeKnowledge () {
      this.removeKnowledgeSub(this.selectedKnowledge)
    },

    // 
    removeKnowledgeSub (knowledge) {
      // 
      knowledge.children.forEach(element => {
        this.removeKnowledgeSub(element)
      })

      // 
      let data = { knowledgeNumber: knowledge.id }
      this.$store.dispatch('deleteKnowledge', data)
    },

    // 
    updateKnowledge () {
      let k = {}
      k.knowledgeNumber = this.selectedKnowledge.id
      k.knowledgeName = this.inputKnowledgeName
      this.$store.dispatch('updateKnowledge', k)
    },

    // 
    updateKnowledgeContent () {
      let k = {}
      k.knowledgeNumber = this.selectedKnowledge.id
      k.knowledgeContent = this.instance.getContent()
      k.knowledgeLatex = this.getLatexString()
      this.$store.dispatch('loading', true)
      this.putRequest(this.httpUrl.knowledge, k).then(response => {
        this.$store.dispatch('loading', false)

        //TODO 
        this.selectedKnowledge.content = this.inputKnowledgeContent

        //
        this.inputKnowledgeContent = ''
        this.editContent = false

        // store
        this.$store.commit('setKnowledgeList', [])
        this.getKnowledgeList()
      }).catch(error => {
        this.$store.dispatch('loading', false)
      })
    },

    getLatexString () {
      let latexString = ''
      let childNodes = this.instance.body.childNodes[0].childNodes
      childNodes.forEach(node => {
        if (node.nodeType === 1) {
          if (node.className === 'aurthur-math-ky') {
            let s = '$' + node.dataset.latex + '$'
            latexString += s
          }
        } else if (node.nodeType === 3) {
          latexString += node.nodeValue
        }
      })
      return latexString
    }
  },
  beforeDestroy () {
    if (this.instance !== null && !this.instance.destroy) {
      this.instance.destroy();
    }
  }
}
</script>

<style lang="scss" scoped>
.knowledge-grade {
  position: absolute;
  top: 0;
  bottom: 0;

  width: 90px;
}

.knowledge-knowledge {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100px;
  overflow: auto;

  width: 290px;
}

.knowledge-detail {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 400px;
}

.border {
  border: $border-solid;
  border-radius: 4px;
}

.box-head {
  position: absolute;
  width: 100%;
  height: 40px;
  padding: 0 10px;
  display: flex;
  align-items: center;

  background-color: $color-gray;
}

.box-main {
  width: 100%;
  top: 40px;
  bottom: 0;
  position: absolute;
  overflow-y: auto;
}

.label {
  flex: 1;
}

.knowledge-highlight {
  font-weight: bold;
}
</style>




