<template>
  <div class="bqteam-main-wrap-inner">
    <div class="sjyl">

      <div class="head">
        <el-button type="text" icon="el-icon-back" @click="$router.go(-1)"></el-button>
        <span class="ml">{{paperTitle}}</span>
      </div>

      <div class="paper-preview">
        <!--  -->
        <div class="paper-content">
          <paper-content></paper-content>
          <paper-content-styled ref="paperContentStyled" v-show="false"></paper-content-styled>
        </div>

        <!--  -->
        <div class="operation">
          <el-button class="paper-download-btn" type="primary" icon="el-icon-download" @click="clickDownloadPaperBtn"></el-button>
          <div class="paper-operate">
            <el-button class="operation-btn" size="small" @click="showScoreSetting = true" icon="el-icon-setting"></el-button>
            <el-button class="operation-btn" size="small" @click="showPaperAnswerCard = true" icon="el-icon-printer"></el-button>
          </div>

          <!--  -->
          <papre-struct/>

          <!--  -->

          <paper-statistics :exerciseListClassified="exerciseListClassified" />

          <!--  -->

          <paper-difficulty :exerciseList="exerciseList" />

          <!--  -->
          <paper-knowledgepoint :exerciseList="exerciseList" />

        </div>
      </div>

      <!--  -->
      <el-dialog title="" :visible.sync="showScoreSetting">
        <paper-score/>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showScoreSetting = false"> </el-button>
          <el-button type="primary" @click="showScoreSetting = false"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="showDownloadOption">
        <span></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="downloadPaper(true)"> </el-button>
          <el-button type="primary" @click="downloadPaper(false)"></el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="showPaperAnswerCard">
        <label class="radio"><input type="radio" value="0" v-model="paperAnswerCardStyle" /></label>
        <label class="radio"><input type="radio" value="1" v-model="paperAnswerCardStyle" /></label>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showPaperAnswerCard = false"> </el-button>
          <el-button type="primary" @click="showPaperAnswerCard = false"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="showPaperDown">
        <span></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showPaperDown = false"> </el-button>
          <el-button type="primary" @click="setPaperDown"> </el-button>
        </span>
      </el-dialog>

    </div>
  </div>

</template>

<script>
import papreStruct from "./components/paper-struct"
import paperStatistics from "./components/paper-statistics"
import paperKnowledgepoint from "./components/paper-knowledgepoint"
import paperDifficulty from "./components/paper-difficulty"
import paperScore from "./components/paper-score"
import paperContent from "./components/paper-content"
import paperContentStyled from "./components/paper-content-styled"
import {
  MOVE_TYPE,
  MOVE_EXERCISE,
  REMOVE_TYPE,
  REMOVE_EXERCISE
} from "@/store/mutation-types";

export default {
  components: {
    papreStruct,
    paperStatistics,
    paperKnowledgepoint,
    paperDifficulty,
    paperScore,
    paperContent,
    paperContentStyled
  },
  data () {
    return {
      //
      showScoreSetting: false,
      //
      showDownloadOption: false,
      //
      showPaperAnswerCard: false,
      //
      showPaperDown: false,

      paperAnswerCardStyle: "0",
    };
  },
  computed: {
    paperTitle () {
      return this.$store.state.paper.paper.paperTitle;
    },

    paperAnswerStyle () {
      return this.$store.state.paperStruct.paperAnswerStyle;
    },

    exerciseList () {
      return this.$store.state.paper.exerciseInfoList;
    },

    exerciseListClassified () {
      return this.$store.state.paper.exerciseListClassified;
    }
  },
  mounted () {
    this.checkPaperExist();
  },
  methods: {
    // 
    checkPaperExist () {
      if (this.$store.state.paper.paper.paperTitle === undefined) {
        this.$router.go(-1);
      }
    },

    // 
    clickDownloadPaperBtn () {
      if (this.paperAnswerStyle === "end") {
        // pdf
        this.showDownloadOption = true;
      } else {
        // 
        this.$refs.paperContentStyled.downloadPaperDirectly()
      }
    },

    // 
    // combine=truepdf
    // combine=falsepdf
    downloadPaper (combine) {
      this.showDownloadOption = false;
      this.$refs.paperContentStyled.downloadPaper(combine);
    },

    setPaperDown () {
      this.showPaperDown = false

      this.$store.commit('setPaperDown')

      // 
      let params = this.$store.state.paper.paper
      this.putRequest(this.httpUrl.paper, params).then(response => {
        console.log("");
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.sjyl {
  position: relative;
  width: fit-content;
  margin: auto;
  width: 100%;
  height: 100%;
}

.head {
  border-bottom: $border-solid;
  margin-bottom: 12px;
}

.paper-preview {
  display: flex;
  justify-content: space-around;
  position: absolute;
  left: 0;
  right: 0;
  top: 72px;
  bottom: 0;
}

.paper-content {
  overflow-y: auto;
  height: 100%;
  width: 720px;
}

.operation {
  display: inline-block;
  overflow-y: auto;
  height: 100%;

  width: 260px;

  border: $border-solid;
  margin-left: 10px;

  &-btn {
    width: 100px;
  }
}

.paper-download-btn {
  width: 220px;
  margin: 10px 20px;

  font-size: 20px;
}

.paper-operate {
  display: flex;
  justify-content: space-around;

  margin: 10px 20px;
}
</style>



